  nav.main-nav {
    position: absolute;
    width: 250px;
    height: 100%;
    top: 0;
    right: -250px;
    left: auto;
    bottom: auto;
    background: #fff;
  }
  nav.main-nav.main-nav-dropdown {
    display: none;
    position: absolute;
    width: 100%;
    height: auto;
    top: 90px;
    left: 0;
    right: 0;
  }
}
ul.main-nav__menu {
  list-style-type: none;
  margin: 0;
  padding: 0;
  display: flex;
  align-items: center;
  justify-content: flex-end;
}
ul.main-nav__menu > li {
  position: relative;
  padding-bottom: 15px;
}
ul.main-nav__menu > li > a {
  display: block;
  padding: 15px 20px;
  font: 700 16px/1.2 'Roboto Condensed', arial, sans-serif;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}
ul.main-nav__menu > li > a:hover {
  text-decoration: none;
}
ul.main-nav__menu > li.current-menu-item > a,
ul.main-nav__menu > li.current-menu-ancestor > a {
  color: #fff;
}
ul.main-nav__menu > li:hover > a {
  color: #fff;
}
ul.main-nav__menu > li:hover .main-nav__second {
  left: 50%;
  transform: translateX(-50%);
  opacity: 1;
}
ul.main-nav__menu > li:last-child .main-nav__second:before {
  left: auto;
  right: 15px;
}
ul.main-nav__menu > li:last-child .main-nav__second.tab-show,
ul.main-nav__menu > li:last-child:hover > .main-nav__second {
  left: auto;
  transform: none;
  right: 0;
}
@media only screen and (min-width: 768px) and (max-width: 1023px), only screen and (max-width: 767px) {
  ul.main-nav__menu {
    display: block;
  }
  ul.main-nav__menu > li {
    padding: 0;
    border-bottom: 1px solid #f3f3f3;
  }
  ul.main-nav__menu > li > a {
    position: relative;
    padding: 10px 20px;
    color: #000;
  }
  ul.main-nav__menu > li.menu-item-has-children > a {
    margin-right: 35px;
  }
  ul.main-nav__menu > li.menu-item-has-children.toggle > a > span.main-nav__dropper:after {
    opacity: 0;
    transform: rotate(0deg);
  }
  ul.main-nav__menu > li:hover > a,
  ul.main-nav__menu > li.current-menu-item > a,
  ul.main-nav__menu > li.current-menu-ancestor > a {
    color: #00aeef;
  }
}
ul.main-nav__second {
  list-style-type: none;
  margin: 0;
  padding: 0;
  position: absolute;
  left: -9999em;
