  background-image: url('/wp-content/themes/groot/img/arrows/chevron-down.svg');
  background-repeat: no-repeat;
  background-size: 20px auto;
  background-position: right 5px top 21px;
}
select::-ms-expand {
  /* for IE 10+ */
  display: none;
}
select[multiple] {
  background-image: none;
  height: 100px;
}
textarea {
  height: 150px;
}
input[type=checkbox],
input[type=radio] {
  position: absolute;
  left: -9999em;
  opacity: 0;
}
input[type=checkbox] + label,
input[type=radio] + label {
  position: relative;
  display: inline-block;
  margin: 0 5px 20px 0;
  padding: 0 0 0 30px;
  font-weight: normal;
}
input[type=checkbox] + label:before,
input[type=radio] + label:before {
  content: "";
  display: block;
  width: 20px;
  height: 20px;
  position: absolute;
  top: 2px;
  left: 0;
  background: #fff;
  border: 1px solid #ccc;
  font-size: 20px;
  line-height: 20px;
  text-align: center;
}
input[type=radio] + label:before {
  border-radius: 50%;
  box-shadow: inset 0 0 0 4px #fff;
}
input[type=radio]:checked + label:before {
  background-color: #000;
}
input[type=checkbox]:checked + label:before {
  content: "\2713";
}
input[type="radio"][disabled],
input[type="checkbox"][disabled] {
  cursor: not-allowed;
}
input[type="radio"][disabled] + label:before,
input[type="checkbox"][disabled] + label:before {
  cursor: not-allowed;
}
/* PREV/NEXT BUTTONS */
.post-pagination {
  margin: 40px 0;
  display: flex;
  justify-content: space-between;
}
@media only screen and (max-width: 767px) {
  .post-pagination {
    margin: 30px -0.5px;
  }
  .post-pagination .nav-previous,
  .post-pagination .nav-next {
    width: 50%;
    padding: 0 0.5px;
    display: flex;
  }
  .post-pagination .nav-previous .btn,
  .post-pagination .nav-next .btn {
    min-width: 0;
    width: 100%;
    padding-left: 10px;
    padding-right: 10px;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 14px;
  }
}
/* PAGINATE_LINKS() */
.post-pagination--numbers {
  display: block;
  margin-left: 0;
  margin-right: 0;
  text-align: center;
}
.post-pagination--numbers .page-numbers {
  display: inline-block;
  padding: 0 5px;
  font: 700 16px/20px 'Roboto Condensed', arial, sans-serif;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}
.post-pagination--numbers .page-numbers.prev,
.post-pagination--numbers .page-numbers.next {
  position: relative;
}
.post-pagination--numbers .page-numbers.prev svg,
.post-pagination--numbers .page-numbers.next svg {
  display: block;
  width: 15px;
  position: absolute;
  top: 0;
}
.post-pagination--numbers .page-numbers.prev svg path,
.post-pagination--numbers .page-numbers.next svg path {
  fill: #00aeef;
  transition: all 250ms ease;
}
.post-pagination--numbers .page-numbers.prev {
  padding-left: 20px;
}
.post-pagination--numbers .page-numbers.prev svg {
  left: 0;
}
.post-pagination--numbers .page-numbers.next {
  padding-right: 20px;
}
.post-pagination--numbers .page-numbers.next svg {
  right: 0;
}
.post-pagination--numbers .page-numbers:hover {
  color: #000;
  text-decoration: none;
}
.post-pagination--numbers .page-numbers:hover svg path {
  fill: #000;
}
.post-pagination--numbers .page-numbers.dots {
  padding: 0;
}
@media only screen and (max-width: 529px)  {
  .post-pagination--numbers {
    display: flex;
    justify-content: space-between;
  }
  .post-pagination--numbers .page-numbers {
    display: none;
  }
  .post-pagination--numbers .page-numbers.prev,
  .post-pagination--numbers .page-numbers.next {
    display: block;
  }
}
