.accordion dt {
  position: relative;
  margin-top: 5px;
  background: #00aeef;
  transition: all 0.25s ease;
}
.accordion dt .accordion__title {
  position: relative;
  margin: 0;
  z-index: 5;
}
.accordion dt .accordion__title > a {
  display: block;
  padding: 20px 20px 20px 45px;
  color: #fff;
}
.accordion dt .accordion__title > a:hover {
  text-decoration: none;
}
.accordion dt:before,
.accordion dt:after {
  content: "";
  display: block;
  position: absolute;
  top: 32px;
  left: 15px;
  width: 16px;
  height: 2px;
  background: #fff;
}
.accordion dt:after {
  transform: rotate(90deg);
  transition: all 250ms ease;
}
.accordion dt.active:after {
  transform: rotate(0);
}
.accordion dd.accordion__content {
  display: none;
  margin: 0;
  padding: 25px 20px 25px 45px;
  background: #f3f3f3;
}
.accordion dd.accordion__content:before,
.accordion dd.accordion__content:after {
  content: " ";
  /* 1 */
  display: table;
  /* 2 */
}
.accordion dd.accordion__content:after {
  clear: both;
}
